import Prism from 'prism-es6'
import 'prism-es6/themes/prism-tomorrow.css'

export default {
  name: 'FlCode',

  functional: true,

  props: {
    code: {
      type: String
    },

    language: {
      type: String,
      default: 'javascript'
    }
  },

  render: function(h, ctx) {
    const code =
      ctx.props.code ||
      (ctx.children && ctx.children.length > 0 ? ctx.children[0].text : '')

    const pre = h('code', {
      domProps: {
        innerHTML: Prism.highlight(code, Prism.languages.javascript, 'javascript')
      }
    })

    return h(
      'pre',
      {
        class: {
          'fl-content': true,
          'language-javascript': true
        }
      },
      [ pre ]
    )
  },

  computed: {
    getContent() {
      return this.source
    }
  }
}
